import React from "react";
import { Col, Row } from "react-bootstrap";
import {
  SiJavascript,
  SiTypescript,
  SiReact,
  SiRedux,
  SiNodeDotJs,
  SiNextDotJs,
  SiGatsby,
  SiMongodb,
  SiPython,
  SiGraphql,
} from "react-icons/si";

function Techstack() {
  return (
    <Row style={{ justifyContent: "center", paddingBottom: "50px" }}>
      <Col xs={4} md={2} className="tech-icons">
        <SiJavascript />
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <SiTypescript />
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <SiReact />
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <SiRedux />
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <SiNodeDotJs />
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <SiNextDotJs />
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <SiGatsby />
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <SiMongodb />
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <SiPython />
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <SiGraphql />
      </Col>
    </Row>
  );
}

export default Techstack;
